.form__sendEmailContainer{
    width: 500px;
    height: 700px;
    border-radius: 15px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    background: rgba(133, 124, 141, 0.5);
    position: absolute;
    left: 50%;
    top: 55%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.form_SendEmailInputs{
    width: 365px;
    height: 50px;
    border-radius: 10px;
    border: 0px;
    margin-top: 0px !important;
    padding-left: 10px;
    outline:0px;
    font-size: 18px;
}

.form_sendEmailTitle{
    color: #F7FFF7;
    font-size: 48px;
    text-align: center;
    padding-top: 20px;
}

textarea{
    resize: none;
    height: 200px !important;
}

.form__sendEmailRoomButton{
    width: 265px;
    height: 65px;
    position: absolute;
    top: 115%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    outline:0px;
    border: 2px black solid;
    font-size: 32px;
}

.form__sendEmailInputs{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}


.form__p{
    margin-bottom: 0px !important;
    margin-top: 15px !important;
}

.from__pSendEmailErrors{
    color: red;
    margin-top: 0px !important;
    margin-bottom: 0px !important;
}

.form_SendEmailErrorsDiv{
    text-align: center;
}
